import React, { Component } from 'react';
import { voteAngular , voteReact , voteVuejs } from './actions';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  constructor(props){
    super(props);
    // expect to get a store see...call component
    this.store = this.props.store;
  }

  handleVoteAngular = () => {
    this.store.dispatch(voteAngular());
  }

  handleVoteReact = () => {
    this.store.dispatch(voteReact());
  }

  handleVoteVueJs = () => {
    this.store.dispatch(voteVuejs());
  }


  render() {
    return (
      <div>
        <div className="jumbotron" style={{ 'textAlign': 'center' }}>
          <img src="ctsw_logo.png" height="96" alt="Developer logo" />
          <h2>What is your favorite fron-end framework</h2>
          <h4>Click on the logos below to vote</h4>
          <br />
          <div className="row">
            <div className="col-sx-offset-3 col-xs-2">
              <img src="/assets/img/angular_logo.png" height="96" alt="Vote Angular" onClick={this.handleVoteAngular} />
            </div>
            <div className="col-sx-offset-3 col-xs-2">
              <img src="/assets/img/react_logo.png" height="96" alt="Vote React" onClick={this.handleVoteReact} />
            </div>
            <div className="col-sx-offset-3 col-xs-2">
              <img src="/assets/img/vuejs_logo.png" height="96" alt="Vote VueJs" onClick={this.handleVoteVueJs} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



